var React = require('react')
  , _ = require('lodash');


module.exports = React.createClass({displayName: 'exports',

  getDefaultProps: function(){
    return {
      language: 'js'
    }
  },

  render: function(){
    return (
      React.createElement("pre", null, 
        React.createElement("code", {className:  this.props.language}, 
          this.props.code
        )
      )
    )
  }
})


